"use client";

import { useEditor, EditorContent as TiptapContent } from "@tiptap/react";
import StarterKit from "@tiptap/starter-kit";
import Placeholder from "@tiptap/extension-placeholder";

interface EditorContentProps {
    section: string;
    content: string;
    onChange: (content: string) => void;
}

export const EditorContent = ({ section, content, onChange }: EditorContentProps) => {
    const editor = useEditor({
        extensions: [
            StarterKit,
            Placeholder.configure({
                placeholder: `Edita el contenido del ${section} aquí...`,
            }),
        ],
        content: content,
        onUpdate: ({ editor }) => {
            onChange(editor.getHTML());
        },
        editorProps: {
            attributes: {
                class: "prose max-w-none focus:outline-none min-h-[500px] p-4",
            },
        },
    });

    return (
        <div className="flex-1 p-4">
            <div className="border border-gray-200 rounded-lg">
                <TiptapContent editor={editor} />
            </div>
        </div>
    );
};
