"use client";

import { Editor } from "@tiptap/react";
import { BoldIcon, ItalicIcon, ListBulletIcon, PhotoIcon, DocumentDuplicateIcon } from "@heroicons/react/24/outline";

interface EditorToolbarProps {
    editor: Editor | null;
}

interface ToolbarButton {
    icon: React.ElementType;
    label: string;
    action: (editor: Editor) => void;
    isActive?: (editor: Editor) => boolean;
}

export const EditorToolbar = ({ editor }: EditorToolbarProps) => {
    if (!editor) {
        return null;
    }

    const tools: ToolbarButton[] = [
        {
            icon: BoldIcon,
            label: "Negrita",
            action: (editor) => editor.chain().focus().toggleBold().run(),
            isActive: (editor) => editor.isActive("bold"),
        },
        {
            icon: ItalicIcon,
            label: "Cursiva",
            action: (editor) => editor.chain().focus().toggleItalic().run(),
            isActive: (editor) => editor.isActive("italic"),
        },
        {
            icon: ListBulletIcon,
            label: "Lista",
            action: (editor) => editor.chain().focus().toggleBulletList().run(),
            isActive: (editor) => editor.isActive("bulletList"),
        },
        {
            icon: PhotoIcon,
            label: "Imagen",
            action: () => {
                // Implementaremos esto en la siguiente fase
                console.log("Image clicked");
            },
        },
        {
            icon: DocumentDuplicateIcon,
            label: "Variable",
            action: () => {
                // Implementaremos esto en la siguiente fase
                console.log("Variable clicked");
            },
        },
    ];

    return (
        <div className="border-b border-gray-200 p-2 flex gap-2">
            {tools.map((tool) => (
                <button
                    key={tool.label}
                    onClick={() => tool.action(editor)}
                    className={`p-2 rounded-md tooltip ${
                        tool.isActive?.(editor) ? "bg-gray-100 text-blue-600" : "hover:bg-gray-100 text-gray-600"
                    }`}
                    title={tool.label}
                >
                    <tool.icon className="h-5 w-5" />
                </button>
            ))}
        </div>
    );
};
